.email,.subject,.message{
    border-radius: 0;
    padding: 15px 0px 10px;
    height: auto;
    border: 0;
    border-bottom: 1px solid #ccc;
    background-color: transparent;
    margin-bottom: 20px;
    font-size: 15px;
    width: 585px;
}

.email:focus,
.subject:focus,
.message:focus {
    border: none;
    background-color: transparent;
    color: #fff;
}

.email::-webkit-input-placeholder,
.subject::-webkit-input-placeholder,
.message::-webkit-input-placeholder{
    color: #fff;
    font-size: 18px;
} 

.message{
    resize: none;
}

